:root {
  --flex-item: 5;
  --flex-item-gap: 20px;
  --main-text-color: #111;
  --main-text-color-60: rgba(17, 17, 17, .6);
  --extra-text-color: #4f2ee8;
  --sub-text-color: #b4afaf;
  --light-text-color: #fff;
  --light-text-color-60: rgba(255, 255, 255, .6);
  --place-text-color: #eac645;
  --dark-text-color-60: rgba(0, 0, 0, .6);
  --main-bg-color: #111;
  --extra-bg-color: #4f2ee8;
  --place-bg-color: #eac645;
  --bg-color: #f6f6f6;
  --extra-border-color: #4f2ee8;
  --extra-border-color-40: rgba(79, 46, 232, .4);
  --empty: #fff;
  --transition-property: color, background, box-shadow;
  --transition-duration: .3s;
  --transition-timing-function: ease-out;
}

*, :before, :after {
  box-sizing: border-box;
}

body {
  color: var(--main-text-color);
  letter-spacing: -.04em;
  font-family: DM Sans, sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.28571;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 375px) {
  .container {
    max-width: 375px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1440px) {
  .container {
