  display: flex;
}

.flex-container-item {
  flex-basis: calc( (100% - (var(--flex-item)  - 1) * var(--flex-item-gap) ) / var(--flex-item) );
}

.main-block > .container {
  flex-direction: column;
  gap: 40px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .main-block > .container {
    gap: 100px;
  }
}

@media screen and (min-width: 1440px) {
  .main-block > .container {
    flex-direction: row;
    gap: 40px;
  }
}

.asides {
  flex-direction: column;
  gap: 40px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .asides {
    gap: 32px;
  }
}

@media screen and (min-width: 1440px) {
  .asides {
    flex: auto;
    gap: 86px;
